<template>
	<view class="content">
		<!-- 头部 -->
		<view class="navbar">
			<view v-for="(item, index) in navList" :key="index" class="nav-item"
				:class="{ current: tabCurrentIndex === index }" @click="tabClick(index)">
				<text> {{ item.text }}</text>

				<!-- <image :src="item.imageSrc"></image> -->
			</view>
		</view>
		<!-- 显示区域 -->
		<view class="list" v-for="(item, index) in navList" :key="index" v-if="tabCurrentIndex === index">
			{{ item.state }}
		</view>
	</view>
</template>

<script>
	export default {
		components: {

		},
		data() {
			return {
				tabCurrentIndex: 0,
				navList: [{
						state: 0,
						text: '租房',
						orderList: [],
						imageSrc: "@/static/test/1.png"
					},
					{
						state: 1,
						text: '合租',
						orderList: []
					},
					{
						state: 2,
						text: '出租',
						orderList: []
					},

				]
			};
		},

		onLoad(options) {
			// 页面显示是默认选中第一个
			this.tabCurrentIndex = 0;
		},

		methods: {
			changeTab(e) {
				this.tabCurrentIndex = e.target.current;
			},
			//顶部tab点击
			tabClick(index) {
				this.tabCurrentIndex = index;
			}
		}
	};
</script>

<style lang="scss">
	.content {
		// background: $page-color-base;
		background: blue($color: #000000);

		height: 100%;
	}

	.navbar {
		text {
			font-size: 45rpx;
		}

		display: flex;
		height: 40px;
		padding: 0 5px;
		background: #fff;
		box-shadow: 0 1px 5px rgba(0, 0, 0, 0.06);
		position: relative;
		z-index: 10;

		.nav-item {
			flex: 1;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 100%;
			font-size: 15px;
			// color: $font-color-dark;
			color: darken($color: #000000, $amount: 0);
			position: relative;

			&.current {
				// color: $base-color;
				color: #3195ee;

				&:after {
					content: '';
					position: absolute;
					left: 50%;
					bottom: 0;
					transform: translateX(-50%);
					width: 44px;
					height: 0;
					border-bottom: 2px solid #3195ee;
				}
			}
		}
	}

